body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.quizApp {
  font-family: 'Open Sans', sans-serif;
}
.quiz {
  background: #b92b27; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    left,
    #1565c0,
    #b92b27
  );
  background: linear-gradient(
    to right,
    #1565c0,
    #b92b27
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding-bottom: 10em;
}
.polaroid {
  background-color: white;
  max-width: 40%;
  max-height: 400%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin: auto;
  border-radius: 8px;
}
.container {
  text-align: center;
  padding: 15px 20px;
  background-color: gray;
  margin: auto;
  max-height: 20em;
  max-width: 20em;
}
.btn {
  border: 2px solid white;
  background-color: transparent;
  color: white;
  font-weight: bold;
  margin-bottom: 0.1em;
  width: 10em;
  height: 3em;
}

.btn:hover {
  border: 2px solid rgb(43, 42, 42);
  color: #fff;
  background: transparent;
}

.image {
  max-height: 600px;
  max-width: 600px;
  overflow: hidden;
}
.btn3 {
  margin-bottom: 4em;
  margin-left: auto;
  margin-right: auto;
  border: 6px solid white;
  color: chocolate;
  font-weight: bold;
  font-size: 25px;
  background-color: transparent;
}
.btn3:hover {
  border: 2px solid rgb(43, 42, 42);
  color: #fff;
  background: blueviolet;
}
.ans {
  margin-top: 5em;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1em;
  border: 3px solid white;
  background-color: wheat;

  width: 25em;
}
.hint {
  border: 2px solid white;
  margin-left: auto;
  margin-right: auto;
  margin-top: 2em;
  max-width: 40%;
  font-weight: bolder;
  background-color: orange;
  font-size: 35px;

  -webkit-align-content: center;

          align-content: center;
}
.hints {
  border: 2px solid white;
  margin-left: auto;
  margin-right: auto;
  background: rgb(60, 60, 60);
  background: rgba(60, 60, 60, 0.4);
  color: black;
  font-weight: 500;
  font-size: 25px;

  max-width: 40%;

  -webkit-align-content: center;

          align-content: center;
}

.hints a {
  color: black;
  opacity: 1;
  font-size: 35px;
}
.footer2 {
  background-color: white;
  align: center;
  font-weight: bolder;
  color: rgb(158, 90, 65);
}

.table {
  font-family: "Open Sans", sans-serif;
}
.table {
  margin: 1em;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.table .header,
.row {
  display: -webkit-flex;
  display: flex;
}
.table .header div,
.table .row div {
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 0.2rem 0.4em;
  border: 1px solid #eeeeee;
}
.table .header {
  background-color: #eeeeee;
}
.table .header div {
  cursor: pointer;
}

.icon {
  height: 85px;
  width: 85px;
}

.rules {
  text-align: left;
}
@-webkit-keyframes change {
  0% {
    background: #0918ee;
    background: -webkit-linear-gradient(to left, #00ff55, #b909ee);
    background: -webkit-linear-gradient(right, #00ff55, #b909ee);
    background: linear-gradient(to left, #00ff55, #b909ee);
  }
  25% {
    background-color: rgb(153, 0, 255);
    background: -webkit-linear-gradient(to left, #5eff00, #096cee);
    background: -webkit-linear-gradient(right, #5eff00, #096cee);
    background: linear-gradient(to left, #5eff00, #096cee);
  }
  50% {
    background-color: rgb(0, 255, 234);
    background: -webkit-linear-gradient(to left, #ffe600, #09d3ee);
    background: -webkit-linear-gradient(right, #ffe600, #09d3ee);
    background: linear-gradient(to left, #ffe600, #09d3ee);
  }
  75% {
    background-color: rgb(145, 216, 14);
    background: -webkit-linear-gradient(to left, #e100ff, #09ee8f);
    background: -webkit-linear-gradient(right, #e100ff, #09ee8f);
    background: linear-gradient(to left, #e100ff, #09ee8f);
  }
  100% {
    background: #ee0979;
    background: -webkit-linear-gradient(to left, #ff6a00, #ee0979);
    background: -webkit-linear-gradient(right, #ff6a00, #ee0979);
    background: linear-gradient(to left, #ff6a00, #ee0979);
  }
}

@keyframes change {
  0% {
    background: #0918ee;
    background: -webkit-linear-gradient(to left, #00ff55, #b909ee);
    background: -webkit-linear-gradient(right, #00ff55, #b909ee);
    background: linear-gradient(to left, #00ff55, #b909ee);
  }
  25% {
    background-color: rgb(153, 0, 255);
    background: -webkit-linear-gradient(to left, #5eff00, #096cee);
    background: -webkit-linear-gradient(right, #5eff00, #096cee);
    background: linear-gradient(to left, #5eff00, #096cee);
  }
  50% {
    background-color: rgb(0, 255, 234);
    background: -webkit-linear-gradient(to left, #ffe600, #09d3ee);
    background: -webkit-linear-gradient(right, #ffe600, #09d3ee);
    background: linear-gradient(to left, #ffe600, #09d3ee);
  }
  75% {
    background-color: rgb(145, 216, 14);
    background: -webkit-linear-gradient(to left, #e100ff, #09ee8f);
    background: -webkit-linear-gradient(right, #e100ff, #09ee8f);
    background: linear-gradient(to left, #e100ff, #09ee8f);
  }
  100% {
    background: #ee0979;
    background: -webkit-linear-gradient(to left, #ff6a00, #ee0979);
    background: -webkit-linear-gradient(right, #ff6a00, #ee0979);
    background: linear-gradient(to left, #ff6a00, #ee0979);
  }
}

.navbar {
  font-family: "Cabin", sans-serif;
  background: rgb(43, 42, 42);
  overflow: hidden;
  padding: 1em;
  width: 100%;
  position: fixed;
}

.navbar h2 {
  font-family: "Kaushan Script", cursive;
  color: #fff;
  font-weight: bold;
  font-size: 3.5em;
}

.navbar a {
  color: #fff;
  text-decoration: none;
  display: inline-block;
  padding: 0 2em;
  font-size: 1.15em;
}

.navbar a:hover {
  color: rgb(88, 73, 73);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.navWide {
  display: none;
  margin: 0 auto;
}

.wideDiv {
  text-align: center;
}

@media only screen and (min-width: 480px) {
  .navWide {
    display: block;
  }

  .navNarrow {
    display: none;
  }
}

.end {
  font-family: 'Open Sans', sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.btn {
