  color: red;
}
.footer {
  color: red;
  padding: 1 em;
}
.form-group {
  max-width: 400px;
  margin: 50px auto;

  border-radius: 2px;
  padding: 20px;
  font-family: Georgia, "Times New Roman", Times, serif;
}

input {
  font-size: 18px;
  padding: 10px 10px 10px 5px;
  display: block;
  width: 300px;
  border: none;
  border-bottom: 1px solid #757575;
}
input:focus {
  outline: none;
}
